.taskGrid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90%;
    height: 90%;
}

.taskGrid--row {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50%;
}

.taskGrid--col {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 100%;
}