.taskCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 85%;
    height: 90%;
    background-color: rgba(0, 0, 0, 0.03);
    border-radius: 1.2rem;
}

.taskCard--cover_container {
    width: 100%;
    height: 85%;
    border-radius: 1.2rem 1.2rem 0 0;
}

.taskCard--cover {
    width: 100%;
    height: 100%;
    border-radius: 1.2rem 1.2rem 0 0;
}

.taskCard--info_container {
    display: flex;
    align-items: center;
    /* justify-content: center; */
    width: 100%;
    height: 15%;
    font-size: 1rem;
    padding-left: 5%;
}

.taskCard--taskName {
    margin-right: 7%;
}

.taskCard--taskName_hint {
    margin-left: 5px;
    font-weight: 500;
}

.taskCard--info_hint {
    font-weight: 500;
}